<template>
  <div class="login">
    <div class="form">
      <div class="choose">
        <div :class="indexflag == 0 ? 'chose' : ''" @click="choose(0)">
          账号登录
        </div>
        <div :class="indexflag == 1 ? 'chose' : ''" @click="choose(1)">
          验证码登录
        </div>
      </div>
      <div class="form1" v-show="indexflag == 0">
        <div class="username">
          <span class="left"><span style="color: red">*</span> 账号</span>
          <input
            type="text"
            class="usernames"
            placeholder="请输入您的账号"
            v-model="username"
          />
        </div>
        <div class="usershows" :style="{ opacity: usershow }">请输入账号</div>
        <div class="password">
          <span class="left"><span style="color: red">*</span> 密码</span>
          <input
            type="password"
            class="passwords"
            placeholder="请输入您的密码"
            v-model="password"
          />
        </div>
        <div class="passshows" :style="{ opacity: passshow }">请输入密码</div>
        <div class="login1" @click="login">账号登录</div>
        <div class="login2">微信扫码登录</div>
        <div class="register">没有账号？点此去注册>></div>
      </div>
      <div class="form2" v-show="indexflag == 1">
        <div class="username">
          <span class="left"><span style="color: red">*</span> 手机号</span>
          <div class="phone">
            <span class="area">+86</span>
            <input
              type="text"
              class="usernames"
              placeholder="请输入您的手机号"
              v-model="phone"
            />
          </div>
        </div>
        <div class="usershows" :style="{ opacity: phoneshow }">
          请输入手机号
        </div>
        <div class="password">
          <span class="left"><span style="color: red">*</span> 验证码</span>
          <div class="codes">
            <input
              type="password"
              class="passwords"
              placeholder="请输入验证码"
              v-model="code"
            />
            <button
              ref="coder"
              :disabled="disabled"
              type="button"
              :class="disabled?'codess':'active'"
              @click="codelogin"
            >
              获取验证码
            </button>
          </div>
        </div>
        <div class="passshows" :style="{ opacity: codeshow }">请输入验证码</div>
        <div class="login1">验证码登录</div>
        <div class="register">没有账号？点此去注册>></div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref, reactive, watch, onMounted, onBeforeUnmount } from "vue";
export default {
  setup() {
    let indexflag = ref(0);
    let username = ref("");
    let phone = ref("");
    let code = ref("");
    let password = ref("");
    let usershow = ref("0");
    let passshow = ref("0");
    let phoneshow = ref("0");
    let codeshow = ref("0");
    let disabled = ref(true);
    let coder = ref(null);
    let time = ref(59);
    let timer = null;
    function choose(e) {
      console.log(e);
      indexflag.value = e;
    }
    function login() {
      axios
        .post("https://znzz.tech/login/", {
          username: username.value,
          password: password.value,
        })
        .then((res) => {
          if (res.data.code == 1000) {
            localStorage.setItem("token", res.data.skey);
            window.location.href = "http://www.jczpin.com/#/userdash";
          } else {
            alert("用户未注册或者账号密码错误");
          }
        });
    }

    onMounted(() => {});
    onBeforeUnmount(()=>{
      clearInterval(timer)
    })
    watch(username, (news) => {
      console.log(news);
      if (news !== "") {
        usershow.value = "0";
      } else {
        usershow.value = "1";
      }
    });
    watch(password, (news) => {
      console.log(news);
      if (news !== "") {
        passshow.value = "0";
      } else {
        passshow.value = "1";
      }
    });
    watch(phone, (news) => {
      if (news !== "") {
        phoneshow.value = "0";
        if (news.length == 11) {
          disabled.value = false;
          console.log(disabled);
        } else {
          disabled.value = true;
        }
      } else {
        phoneshow.value = "1";
        disabled.value = true;
      }
    });
    watch(code, (news) => {
      console.log(news);
      if (news !== "") {
        codeshow.value = "0";
      } else {
        codeshow.value = "1";
      }
    });
    const codelogin = () => {
      axios
        .post("https://znzz.tech/login_mobile/", {
          mobile: phone.value,
          type: "1",
        })
        .then((res) => {
          timer = setInterval(() => {
            if (time.value == 0) {
              clearInterval(timer);
              coder.value.innerHTML = "获取验证码";
            } else {
              time.value--;
              coder.value.innerHTML = time.value + "s后重新获取";
              disabled.value =true
            }
          }, 100);
        });
    };
    return {
      indexflag,
      choose,
      username,
      password,
      login,
      usershow,
      passshow,
      phone,
      phoneshow,
      code,
      codeshow,
      disabled,
      codelogin,
      coder,
      timer,
    };
  },
};
</script>

<style scoped>
.login {
  width: 100%;
  height: 100vh;
  background-image: url("../assets/bck.png");
  box-sizing: border-box;
  padding: 10% 0 0 0;
}
.form {
  position: relative;
  width: 480px;
  box-sizing: border-box;
  min-height: 410px;
  padding: 0 35px 30px;
  margin: 0 auto;
  overflow: hidden;
  background-color: #fff;
  border-radius: 40px;
}
.choose {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  margin: 50px 56px 20px 56px;
  cursor: pointer;
}
.chose {
  width: 130px;
  box-sizing: border-box;
  color: #1890ff;
  position: relative;
}
.chose::after {
  content: "";
  display: block;
  height: 2px;
  position: relative;
  background-color: #1890ff;
  top: 10px;
}
.form1 {
  text-align: left;
  margin-top: 70px;
  vertical-align: middle;
  font-size: 14px;
  color: #606266;
  font-weight: bold;
  line-height: 38px;
}
.form2 {
  text-align: left;
  margin-top: 70px;
  vertical-align: middle;
  font-size: 14px;
  color: #606266;
  font-weight: bold;
  line-height: 38px;
}
.phone {
  position: relative;
}
.area {
  width: 30px;
  position: absolute;
  left: 0;
  box-sizing: border-box;
  padding-left: 5px;
}
.area::after {
  content: "";
  display: block;
}
.form2 .usernames {
  padding-left: 40px;
}
.form2 .codes {
  display: flex;
  justify-content: space-between;
}
.form2 .codes .passwords {
  width: 198px;
}
.codes button {
  width: 111px;
  height: 38px;
}
.username,
.password {
  display: flex;
  margin-top: 10px;
}
.usershows {
  color: red;
  box-sizing: border-box;
  padding-left: 55px;
  font-weight: 400;
  opacity: 0;
}
.passshows {
  color: red;
  box-sizing: border-box;
  padding-left: 55px;
  font-weight: 400;
  opacity: 0;
}
.left {
  margin-right: 15px;
  box-sizing: border-box;
}
.usernames,
.passwords {
  box-sizing: border-box;
  width: 308px;
  height: 38px;
  padding: 0 15px;
  outline: none;
  border-radius: 4px;
  border: 1px solid #ccc;
}
::placeholder {
  color: #ccc;
}
.login1 {
  width: 100%;
  background-color: rgb(37, 110, 253);
  color: rgb(255, 255, 255);
  text-align: center;
  border-radius: 20px;
  padding: 12px 23px;
  box-sizing: border-box;
  line-height: normal;
  margin-bottom: 30px;
  cursor: pointer;
  font-weight: 400;
}
.login2 {
  width: 100%;
  background-color: rgb(37, 110, 253);
  color: rgb(255, 255, 255);
  text-align: center;
  border-radius: 20px;
  padding: 12px 23px;
  box-sizing: border-box;
  line-height: normal;
  cursor: pointer;
  font-weight: 400;
}
.register {
  font-weight: 400;
  color: black;
  font-size: 16px;
  margin-top: 50px;
  text-align: center;
  cursor: pointer;
}
.codess{
  color: #ccc;
}
.active {
  color: #fff;
  background-color: #1890ff;
  cursor: pointer;

}
</style>